@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
header{
    position: fixed;
    top: 0;
    left: 0;
    text-align: center;
    background-color: var(--color-red);
    color: #FFF;
    width: 100%;
    padding: .5rem;
    height: 3rem;
    display: grid;
    align-content: center;
    text-transform: uppercase;
}

header h1{
    font-size: 1rem;
    line-height: 1rem;
    text-transform: uppercase;
}
.cometa{
    border-radius: .5rem;
    text-align: center;
    background-color: #FFF;
    padding: 1.5rem;
    color: var(--color-blue);
}

.cometa.hazardous{
    border: 4px solid var(--color-red);
}

.cometa .cometa-circulo{
    margin: auto;
    height: 1.5rem;
    width: 1.5rem;
    background-color: var(--color-red);
    border-radius: 50%;
    margin-bottom: 1rem;
}

.cometa h2{
    font-size: 1rem;
    margin-bottom: 1rem;
}

.cometa .cometa-info{
    margin-bottom: 1rem;
}

.cometa .cometa-info :last-of-type{
    margin-bottom: 0;
}

.cometa .cometa-info h3{
font-size: .75rem;
line-height: 1.4em;
color: var(--color-red);
}

.cometa .cometa-info p{
    font-size: .75rem;
    color: var(--color-blue);
    line-height: 1.4em;
}
main{
    padding: 4rem 1rem 1rem 1rem;
    background-color: var(--color-blue);
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}

main .info{
    grid-column: span 3;
    color:#FFF;
    text-align: center;
}

@media screen and (max-width: 769px) {
    main a{
        grid-column: span 3;
    }
}
.detalhe{
    grid-column: span 3;
}

main#cometa a{
    grid-column: span 3;
}

.btn-home{
    color: var(--color-red);
    background-color: #FFF;
    text-align: center;
    border-radius: .5rem;
    padding: .5rem;
    font-weight: bold;
}

.cometa-hazardous{
    text-transform: uppercase;
    color: var(--color-red);
    font-weight: bold;
}
.grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
    padding: 0 1rem;
}

.item-01{
    grid-column: span 1;
}

.item-02{
    grid-column: span 2;
}

.item-03{
    grid-column: span 2;
}
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    font-weight: normal;
}

:root{
    font-size: 1.5vw;
    line-height: 1.1em;
    --color-red: #FC3D21;
    --color-blue: #0B3D91;
}

html, body{
width: 100%;
height: 100%;
font-family: 'Karla', sans-serif;
line-height: 1.4em;
}

a{
    text-decoration: none;
}

@media screen and (max-width: 769px) {
    :root{
        font-size: 2.1vw;
        line-height: 1.1em;
    }
}

@media screen and (max-width: 576px) {
    :root{
        font-size: 4vw;
        line-height: 1.1em;
    }
}
