@import url('https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap');

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    font-weight: normal;
}

:root{
    font-size: 1.5vw;
    line-height: 1.1em;
    --color-red: #FC3D21;
    --color-blue: #0B3D91;
}

html, body{
width: 100%;
height: 100%;
font-family: 'Karla', sans-serif;
line-height: 1.4em;
}

a{
    text-decoration: none;
}

@media screen and (max-width: 769px) {
    :root{
        font-size: 2.1vw;
        line-height: 1.1em;
    }
}

@media screen and (max-width: 576px) {
    :root{
        font-size: 4vw;
        line-height: 1.1em;
    }
}